import React,{FC} from 'react';
import styled from 'styled-components';

import {CardProps} from "./Card.types";
import Img from "../Img/Img";
import Text from "../Text/Text";

const StyledCard = styled.text<CardProps>`
    line-height: 1;
    font-size: 18px;
    font-weight: 700;
    font-weight: bold;
    display: block;
    border: 0;
    color: #000000;
    background-color: ${props => props.backgroundColor};
    cursor: ${props => props.disabled ? "not-allowed" : "cursor"};
    opacity: ${props => props.disabled ? 0.5 : 1};
`;

const Card: FC<CardProps> = ({disabled, title, text, src, img_width, backgroundColor, onClick, ...props}) => {
    return (
        <div>
        <Img src={src} width={img_width} />
        <StyledCard onClick={onClick} disabled={disabled} {...props}>
            {title}
        </StyledCard>
        <Text text={text} />
        </div>
    )
};

export default Card;